const isString = str => typeof str === 'string';

export const isHiragana = (str = '') => {
    return isString(str) ? /^[ぁ-ん|ー]+$/.test(str) : false;
};

export const isKatakana = (str = '') => /^[ァ-ロワヲンー ｧ-ﾝﾞﾟ|^　|]*$/.test(str);

export const isEmail = str => {
    return isString(str) ?
        /^[a-zA-Z0-9!$&*.=^`|~#%'+\/?_{}-]+@([a-zA-Z0-9_-]+\.)+[a-zA-Z]{2,4}$/.test(str) :
        false;
};

const isHiraganaByCode = char => ((char >= 12353 && char <= 12435) || char === 12445 || char === 12446);

export const toKatakana = src => {
    let c, str = '';
    for(let i = 0, num = src.length; i < num; i++) {
        c = src.charCodeAt(i);
        if (isHiraganaByCode(c)) {
            str += String.fromCharCode(c + 96);
        } else {
            str += src.charAt(i);
        }
    }
    return str;
};
