import React, { Component } from 'react';
import classNames from 'classnames';
import { withStyles } from '@material-ui/core/styles';
import FormControl from '@material-ui/core/FormControl';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import FormHelperText from '@material-ui/core/FormHelperText';
import NavigationIcon from '@material-ui/icons/Navigation';
import Button from '@material-ui/core/Button';
import historykana from 'historykana';

import Layout from '../components/layout';
import SEO from '../components/seo';
import DoneIcon from '../components/done-icon';

import { errorMsg } from '../constants';
import { isKatakana, toKatakana } from '../utils';

const styles = theme => ({
    container: {
        // display: 'flex',
        // flexWrap: 'wrap'
    },
    textField: {
        width: 200,
        marginRight: theme.spacing.unit
    },
    floater: {
        float: 'left'
    },
    list: {
        marginBottom: theme.spacing.unit,
        clear: 'both',
        lineHeight: 0,
        display: 'table'
    },
    error: {
        fontSize: '0.15rem',
        color: '#ff2929',
        margin: theme.spacing.unit
    },
    fab: {
        margin: theme.spacing.unit
    },
    noAllow: {
        cursor: 'not-allowed',
        pointerEvents: 'auto'
    },
    icon: {
        position: 'absolute',
        top: '39%',
        right: '5%'
    }
});

class IndexPage extends Component {
    state = {
        familyName: '',
        isErrorFamilyName: false,
        familyNameKana: '',
        isErrorFamilyNameKana: '',
        firstName: '',
        isErrorFirstName: '',
        firstNameKana: '',
        isErrorFirstNameKana: '',
        status: false
    }

    constructor(props) {
        super(props);
        this.timer = -1;
        this.historyFamilyName = [];
        this.historyFirstName = [];

        this.onChangeFamilyName = this.onChangeFamilyName.bind(this);
        this.onChangeFamilyNameKana = this.onChangeFamilyNameKana.bind(this);
        this.onChangeFirstName = this.onChangeFirstName.bind(this);
        this.onChangeFirstNameKana = this.onChangeFirstNameKana.bind(this);
        this.onBlurFamilyName = this.onBlurFamilyName.bind(this);
        this.onBlurFamilyNameKana = this.onBlurFamilyNameKana.bind(this);
        this.onBlurFirstName = this.onBlurFirstName.bind(this);
        this.onBlurFirstNameKana = this.onBlurFirstNameKana.bind(this);

        this.checkFamilyName = this.checkFamilyName.bind(this);
        this.checkFamilyNameKana = this.checkFamilyNameKana.bind(this);
        this.checkFirstName = this.checkFirstName.bind(this);
        this.checkFistNameKana = this.checkFistNameKana.bind(this);

        this.checkStatus = this.checkStatus.bind(this);
    }

    /*shouldComponentUpdate(nextProps) {
        return this.props !== nextProps;
    }*/
    
    /**
     * 姓のkeydown
     * @param {*} event 
     */
    onChangeFamilyName(event) {
        if (this.timer !== -1) {
            window.clearTimeout(this._timer);
        }

        this.historyFamilyName.push(event.target.value);
        
        this.setState({
            familyName: event.target.value
        });
        this.timer = window.setTimeout(() => {
            const familyKana = historykana(this.historyFamilyName);
            const familyKatakana = toKatakana(familyKana);
            this.setState({
                familyNameKana: familyKatakana
            });
            this.checkFamilyName();
        }, 300);
    }

    /**
     * 姓がフォーカス外れた
     */
    onBlurFamilyName(event) {
        this.checkFamilyName(event.target.value);
    }

    /**
     * セイをkeydown
     * @param {*} event 
     */
    onChangeFamilyNameKana(event) {
        if (this.timer !== -1) {
            window.clearTimeout(this._timer);
        }
        this.setState({
            familyNameKana: event.target.value
        });
        this.timer = window.setTimeout(() => {
            this.checkFamilyNameKana();
        }, 300);
    }

    /**
     * セイのフォーカス外れた
     * @param {*} event 
     */
    onBlurFamilyNameKana(event) {
        this.historyFamilyName = [];
        this.checkFamilyNameKana(event.target.value);
    }

    onChangeFirstName(event) {
        if (this.timer !== -1) {
            window.clearTimeout(this._timer);
        }
        this.historyFirstName.push(event.target.value);
        this.setState({
            firstName: event.target.value
        });
        this.timer = window.setTimeout(() => {
            const kana = historykana(this.historyFirstName);
            const firstKatakana = toKatakana(kana);
            this.setState({
                firstNameKana: firstKatakana
            });
            this.checkFirstName();
        }, 300);
    }

    onBlurFirstName(event) {
        this.historyFirstName = [];
        this.checkFirstName(event.target.value);
    }

    onChangeFirstNameKana(event) {
        if (this.timer !== -1) {
            window.clearTimeout(this._timer);
        }
        
        this.setState({
            firstNameKana: event.target.value
        });

        this.timer = window.setTimeout(() => {
            this.checkFistNameKana();
        }, 300);
    }

    onBlurFirstNameKana() {
        this.checkFistNameKana();
    }

    /**
     * 姓のバリデート
     */
    checkFamilyName() {
        const { familyName } = this.state;
        this.setState({
            isErrorFamilyName: familyName.length === 0
        });
        this.checkStatus();
    }

    /**
     * セイのvalidate
     */
    checkFamilyNameKana() {
        const { familyNameKana } = this.state;
        if (familyNameKana.length > 0 && isKatakana(familyNameKana)) {
            // ok
            this.setState({
                isErrorFamilyNameKana: ''
            });
        } else if(!isKatakana(familyNameKana)) {
            // type error
            this.setState({
                isErrorFamilyNameKana: 'type'
            });
        } else {
            // required error
            this.setState({
                isErrorFamilyNameKana: 'required'
            });
        }
        this.checkStatus();
    }

    checkFirstName() {
        const { firstName } = this.state;
        this.setState({
            isErrorFirstName: firstName.length === 0
        });
        this.checkStatus();
    }

    /**
     * 名のvalidate
     */
    checkFistNameKana() {
        const { firstNameKana, isErrorFirstNameKana } = this.state;
        if (firstNameKana.length > 0 && isKatakana(firstNameKana)) {
            // ok
            this.setState({
                isErrorFirstNameKana: ''
            });
        } else if(!isKatakana(firstNameKana)) {
            // type error
            this.setState({
                isErrorFirstNameKana: 'type'
            });
        } else {
            // required error
            this.setState({
                isErrorFirstNameKana: 'required'
            });
        }
        this.checkStatus();
    }

    checkStatus() {
        const {
            familyName,
            familyNameKana,
            isErrorFamilyName,
            isErrorFamilyNameKana,
            firstName,
            isErrorFirstName,
            firstNameKana,
            isErrorFirstNameKana
        } = this.state;
        let status = false;
        if (familyName.length > 0 && familyNameKana.length > 0 && firstName.length > 0 && firstNameKana.length > 0) {
            status = !!(!isErrorFamilyName && isErrorFamilyNameKana === '' && !isErrorFirstName && isErrorFirstNameKana === '');
        }
        this.setState({
            status
        });
    }

    render() {
        const { classes } = this.props;
        const {
            isErrorFamilyName,
            isErrorFamilyNameKana,
            familyName,
            firstName,
            familyNameKana,
            firstNameKana,
            isErrorFirstName,
            isErrorFirstNameKana,
            status
        } = this.state;
        const familyNameError = isErrorFamilyName ? (
            <FormHelperText className={classes.error}>{errorMsg.required}</FormHelperText>
        ) : null;
        const familyNameKanaError = isErrorFamilyNameKana.length > 0 ? (
            <FormHelperText className={classes.error}>{errorMsg[isErrorFamilyNameKana]}</FormHelperText>
        ) : null;
        const firstNameError = isErrorFirstName ? (
            <FormHelperText className={classes.error}>{errorMsg.required}</FormHelperText>
        ) : null;
        const firstNameKanaError = isErrorFirstNameKana ? (
            <FormHelperText className={classes.error}>{errorMsg[isErrorFirstNameKana]}</FormHelperText>
        ) : null;
        const btnClassName = classNames(classes.fab, {
            [classes.noAllow]: !status
        });
        const iconFamilyName = familyName.length > 0 && !isErrorFamilyName ? (
            <DoneIcon className={classes.icon} color="primary" />
        ) : null;
        const iconFirstName = firstName.length > 0 && !isErrorFirstName ? (
            <DoneIcon className={classes.icon} color="primary" />
        ) : null;
        // console.log(`isErrorFamilyNameKana: ${isErrorFamilyNameKana}, ${familyNameKana}`);
        const iconFamilyNameKana = familyNameKana.length > 0 && isErrorFamilyNameKana === '' ? (
            <DoneIcon className={classes.icon} color="primary" />
        ) : null;
        const iconFirstNameKana = firstNameKana.length > 0 && isErrorFamilyNameKana === '' ? (
            <DoneIcon className={classes.icon} color="primary" />
        ) : null;
        return (
            <Layout>
                <SEO title="Home" keywords={['gatsby', 'application', 'react']} lang="ja" meta={[]} />
                <h1>Gatsby Form</h1>
                <form className={classes.container} noValidate autoComplete="off">
                    <div className={classes.list}>
                        <div className={classes.floater}>
                            <FormControl>
                                <InputLabel htmlFor="familyName">姓</InputLabel>
                                <Input
                                    className={classes.textField}
                                    id="familyName"
                                    placeholder="姓"
                                    label="姓"
                                    error={isErrorFamilyName}
                                    value={familyName}
                                    onChange={this.onChangeFamilyName}
                                    onBlur={this.onBlurFamilyName}
                                />
                                {iconFamilyName}
                                {familyNameError}
                            </FormControl>
                        </div>
                        <div className={classes.floater}>
                            <FormControl>
                                <InputLabel htmlFor="firstName">名</InputLabel>
                                <Input
                                    className={classes.textField}
                                    id="firstName"
                                    placeholder="名"
                                    label="名"
                                    value={this.state.firstName}
                                    onChange={this.onChangeFirstName}
                                    onBlur={this.onBlurFirstName}
                                />
                                {iconFirstName}
                                {firstNameError}
                            </FormControl>
                        </div>
                    </div>
                    <div className={classes.list}>
                        <div className={classes.floater}>
                            <FormControl>
                                <InputLabel htmlFor="familyNameKana">セイ</InputLabel>
                                <Input
                                    className={classes.textField}
                                    id="familyNameKana"
                                    placeholder="セイ"
                                    label="セイ"
                                    error={!!isErrorFamilyNameKana.length > 0}
                                    value={this.state.familyNameKana}
                                    onChange={this.onChangeFamilyNameKana}
                                    onBlur={this.onBlurFamilyNameKana}
                                />
                                {iconFamilyNameKana}
                                {familyNameKanaError}
                            </FormControl>
                        </div>
                        <div className={classes.floater}>
                            <FormControl>
                                <InputLabel htmlFor="firstNameKana">メイ</InputLabel>
                                <Input
                                    className={classes.textField}
                                    id="firstNameKana"
                                    placeholder="メイ"
                                    label="メイ"
                                    value={this.state.firstNameKana}
                                    onChange={this.onChangeFirstNameKana}
                                    onBlur={this.onBlurFirstNameKana}
                                />
                                {iconFirstNameKana}
                                {firstNameKanaError}
                            </FormControl>
                        </div>
                    </div>
                    <Button
                        className={btnClassName}
                        variant="contained"
                        color="primary"
                        disabled={!status}
                    >
                        <NavigationIcon />
                        SEND
                    </Button>
                </form>
            </Layout>
        );
    }
}

// export default IndexPage;
export default withStyles(styles)(IndexPage);
