import React from 'react';
import { StaticQuery, graphql } from 'gatsby';

import '../styles/main.sass';

const Layout = ({ children }) => (
    <StaticQuery
      query={graphql`
        query SiteTitleQuery {
          site {
            siteMetadata {
              title
            }
          }
        }
      `}
      render={data => (
        <>
          <div>
            {children}
          </div>
        </>
      )}
    />
  );
  
export default Layout
